import React, { useRef, useState, useEffect } from 'react';

import EditorJs from 'react-editor-js';
// import ReadMode from "../components/readMode"
import Axios from "axios"
import { Button, Modal } from "react-bootstrap"
import { EDITOR_JS_TOOLS } from "../lib/editorjs.dependencies"
import { getApiLink } from '../lib/env.lib';
import { Redirect } from "react-router-dom"
export const randNum = (min, max) => {
    let random = Math.round(Math.random() * (+max - +min) + +min)
    return random
}

export const getRandomBanner = () => {
    return `https://i.picsum.photos/id/${randNum(0, 1084)}/1200/400.jpg`
}
const Editor = (props) => {
    let key_i = 0;
    const instanceRef = useRef(null);

    const [show, setShow] = useState(false)
    const [data, setData] = useState(null)
    const [mode, setMode] = useState(null)
    const [api, setApi] = useState("")
    const [redir, setRedirect] = useState(<></>)
    const [ready, setReady] = useState(false)
    const [articleId, setArticleId] = useState("")
    const [banner, setBanner] = useState("NA")

    function handleOpen() {
        setShow(true)
    }

    function handleClose() {
        setShow(false)
    }
    async function handleSave() {
        const savedData = await instanceRef.current.save();

        setData(savedData)
        console.log("savedData", savedData);
    }

    function handleBtnClick() {
        handleSave()
        if (data.blocks.length > 0) {
            let _data = new Object()
            if (props.mode == "edit") {
                _data.article_id = articleId
            } else if (props.mode == "new") {

            }

            if (banner != "NA") {
                _data.article_banner = banner
            }
            _data.xAuthToken = props.auth_token
            _data.title = data.blocks[0].data.text
            _data.content = JSON.stringify(data)
            _data.preview = (data.blocks.length > 2) ? data.blocks[1].data.text : data.blocks[0].data.text

            console.log({ _data })

            Axios.post(`${getApiLink()}/api/v1/${api}`, _data).then((response) => {
                console.log(response)
                if (response.data.success) {
                    if (props.mode == "edit") {
                        props.notyf.success("Saved article")

                    } else {
                        props.notyf.success("Published article")
                    }
                    setRedirect(
                        <>
                            <Redirect to={(() => `/read/${response.data.message.data.article_id}`)()} />
                        </>
                    )
                }
            }).catch(e => {
                if (e) {
                    console.log({ resp: e.response.data })
                    if (e.response.data.error.message != null) {
                        props.notyf.error("" + e.response.data.error.message)
                    } else {
                        props.notyf.error("Error occured")
                    }
                } else {
                    props.notyf.error("Error occured")
                }
            })


        } else {
            props.notyf.error("Invalid article content")
        }
    }

    function handleBannerClick() {
        if (banner == "NA") {
            setBanner(getRandomBanner())
        } else {
            setBanner("NA")
        }
    }
    useEffect(() => {
        if (props.mode == "edit") {
            setMode("Save")
            setApi("updateArticleById")
            if (props.articleData) {
                setData(JSON.parse(props.articleData.content))
            } else {
                setData({ time: Date.now(), blocks: [], version: "2.17.0" })
            }

        } else {
            setMode("Publish")
            setApi("createArticle")
            setData({ time: Date.now(), blocks: [], version: "2.17.0" })
            setBanner("NA")
        }
    }, [])


    useEffect(() => {
        if (props.articleData) {
            setReady(true)
            setArticleId(props.articleData.article_id)
            setData(JSON.parse(props.articleData.content))
            setBanner(props.articleData.article_banner)
        }
    }, [props.articleData])

    return (<>
        {/* <br /> */}
        {redir}
        <div className="w-100 text-center" style={{
            // marginTop: "90px"
        }}>

            {(banner != "NA") ? (<>

                {/* <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                    <img src={banner} alt="&nbsp;" style={{
                        maxHeight: "20vw",
                        // maxWidth: "1200px",
                        width: "100vw",

                    }} />
                </div> */}

                <div className="w-100 show-xs show-sm hide-md">
                    <div className=" text-center mb-2" style={{
                        height: "40vw",
                        width: "100%",
                        overflow: "hidden",
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center"

                    }}>
                        <img src={banner} alt="&nbsp;" style={{
                            height: "100%",
                            width: "auto",
                        }} />
                    </div>
                </div>
            </>) : (<></>)}
        </div>


        {/* </div> */}

        <div className="container">
            {/* <p>{JSON.stringify(data)}</p> */}
            <div className="row">
                <div className="col-md-2">

                    <button className="btn btn-primary mt-2" onClick={handleOpen}>
                        {mode}
                    </button>

                    <Modal show={show}
                        onHide={handleClose}
                        size="lg"
                    // dialogClassName="modal-90w"
                    >
                        <Modal.Header closeButton>
                            <Modal.Title>{mode} Article</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            Would you like to {mode} this article.
                            <br />

                            <h4>preview</h4>

                            

                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleClose}>
                                Close
                            </Button>
                            <Button variant="primary" onClick={handleBtnClick}>
                                {mode}
                            </Button>
                        </Modal.Footer>
                    </Modal>
                    <button className="btn btn-outline-secondary btn-sm mt-1 show-xs show-sm hide-md" onClick={handleBannerClick}>
                        {(() => {
                            if (banner == "NA") {
                                return "Add Banner"
                            } else {
                                return "Remove Banner"
                            }
                        })()}
                    </button>

                    {(() => {
                        if (banner != "NA") {
                            return (
                                <>
                                    <button className="btn btn-sm btn-info show-xs show-sm hide-md" onClick={() => {
                                        setBanner(getRandomBanner())
                                    }}>Randomize</button>
                                </>
                            )
                        }
                    })()}
                </div>
                <div className="col-md-8">
                    <div className="w-100">
                        {(banner != "NA") ? (<>

                            <div className="text-center mb-2 hide-xs hide-sm show-md show-lg show-xl">
                                <img src={banner} alt="&nbsp;" style={{
                                    maxHeight: "20vw",
                                    // maxWidth: "1200px",
                                    width: "100%",

                                }} />
                            </div>

                            {/* <div className="w-100 show-xs show-sm hide-md">
                                <div className=" text-center mb-2" style={{
                                    height: "40vw",
                                    width: "100vw",
                                    overflow: "hidden",
                                    display: "flex",
                                    justifyContent: "center",
                                    alignItems: "center"

                                }}>
                                    <img src={banner} alt="&nbsp;" style={{
                                        height: "100%",
                                        width: "auto",
                                    }} />
                                </div>
                            </div> */}
                        </>) : (<></>)}
                    </div>

                    {(() => {
                        if ((props.mode == "edit" && ready) || (props.mode == "new")) {
                            return (
                                <>
                                    <EditorJs
                                        placeholder="Write an awesome..."
                                        instanceRef={instance => (instanceRef.current = instance)}
                                        onChange={handleSave}
                                        tools={EDITOR_JS_TOOLS}
                                        data={data}
                                    />
                                </>
                            )
                        }
                    })()}

                </div>

                <div className="col-md-2">
                    <button className="btn btn-outline-secondary btn-sm mt-1 hide-xs hide-sm show-md" onClick={handleBannerClick}>
                        {(() => {
                            if (banner == "NA") {
                                return "Add Banner"
                            } else {
                                return "Remove Banner"
                            }
                        })()}
                    </button>

                    {(() => {
                        if (banner != "NA") {
                            return (
                                <>
                                    <button className="btn btn-sm btn-info hide-xs hide-sm show-md mt-1" onClick={() => {
                                        setBanner(getRandomBanner())
                                    }}>Randomize</button>
                                </>
                            )
                        }
                    })()}


                </div>
            </div>

        </div>
    </>
    );
}

export default Editor;